import { Stateless, Component } from '@eui/component';
import { render as litRender, html as litHtml, svg } from 'lit-html';

// Allow users to add the renderer mixin on top of a custom base
/* eslint arrow-body-style:0 */
export const LitMixin = (Base) => {
  return class LitComponent extends Base {
    static register(customName) {
      if (super.register) {
        super.register(customName);
      } else {
        console.error('register function not defined');
      }
    }

    static registerComponents(components) {
      if (super.registerComponents) {
        super.registerComponents(components);
      } else {
        console.error('registerComponents function not defined');
      }
    }

    constructor() {
      super();
      if (this.constructor.style) {
        this._litStyle = litHtml`<style>${this.constructor.style}</style>`;
      }
    }

    /**
     * @protected
     * @function executeRender
     * @description Executes a render on the component using the lit renderer
     */
    executeRender() {
      if (!this.shadowRoot) {
        this.attachShadow({ mode: 'open' });
        this.firstRender = true;
      }
      if (!this.render) {
        throw new Error('render callback not found');
      }
      let result = this.render();
      if (this._litStyle) {
        result = litHtml`
        ${this._litStyle}
        ${result}
        `;
      }

      // Render into the DOM
      litRender(result, this.shadowRoot);
      // ShadyCSS updates
      if (this.compatElementUpdated) {
        this.compatElementUpdated();
      }

      if (this.firstRender) {
        this.firstRender = false;
        this.didUpgrade();
      }
    }
  };
};

// Export a default stack of renderer + standard mixins
class LitStateless extends LitMixin(Stateless) {}
class LitComponent extends LitMixin(Component) {}

export {
  LitStateless,
  LitComponent,
  // Re-export the parts of lit-html users might also want to use
  litHtml as html,
  litRender as render,
  svg,
};

// Note: The following lit-html directives are a potential source of XSS
// as per their docs if not used correctly. Do not export them.
// E-UI SDK will not be a vector for UI attacks
// - unsafeHTML
// - unsafeSVG
// - templateContent

// export lit-html directives
export { asyncAppend } from 'lit-html/directives/async-append';
export { asyncReplace } from 'lit-html/directives/async-replace';
export { cache } from 'lit-html/directives/cache';
export { classMap } from 'lit-html/directives/class-map';
export { ifDefined } from 'lit-html/directives/if-defined';
export { guard } from 'lit-html/directives/guard';
export { live } from 'lit-html/directives/live';
export { createRef } from 'lit-html/directives/ref';
export { ref } from 'lit-html/directives/ref';
export { repeat } from 'lit-html/directives/repeat';
export { styleMap } from 'lit-html/directives/style-map';
export { until } from 'lit-html/directives/until';
export { nothing } from 'lit-html';

// With lit-html 2.0, ifDefined behaviour matches that from ifNull
// No longer require an inhouse directive,
// For backwards compatibility direct all ifNull requests to lit-html directive
// https://github.com/lit/lit/issues/1927
export { ifDefined as ifNull } from 'lit-html/directives/if-defined';

export { definition } from '@eui/component';
